/** @jsx React.DOM */

"use strict";

var React = require("react");
var _ = require("underscore");

/**
 * Search text extry box.
 */
var SearchBox = React.createClass({

    displayName: "SearchBox",

    getInitialState: function() {
        return {value: this.props.initialValue};
    },

    onSubmit: function() {
        var val = this.refs.search.getDOMNode().value;
        this.setState({"value": val});

        //Callback
        if (this.props.onSubmit) {
            this.props.onSubmit(val);
        }
    },
 
    render: function() {
        return (
            React.DOM.form({onSubmit: this.onSubmit}, 
                React.DOM.div({className: "input-group"}, 
                    React.DOM.input({className: "form-control", 
                           type: "search", 
                           ref: "search", 
                           placeholder: "Search", 
                           defaultValue: this.state.value}
                    ), 
                    React.DOM.span({className: "input-group-addon", onClick: this.onSubmit}, 
                        React.DOM.span({className: "glyphicon glyphicon-search"})
                    )
                )
            )
        );
    }
});

module.exports = SearchBox;


